
.contactSection {

  display: flex;
  padding-left:85.33px;
  padding: 40.333px 85.333px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align:center;
  gap: 30px;
  position:relative;
 
}
.contactButton{
border-radius: 10px;
margin-top:20px;
background-color:#FF5A5F;
width:100%;
}
.contact--form--container {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(5, auto);
  row-gap: 20px;
  
  
 
}
.container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 12px;
  row-gap: 12px;
  
}
.contact--label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10.6px;
  flex: 1 0 0;
  width: 100%;
}
.contact--input {
  display: flex;
  font-family: "Roboto";
  padding: 16px;
  align-items: center;
  align-self: stretch;
  border-radius: 8px;
  border: 1.333px solid var(--primary);
  background: var(--white);
 
}
.contactOverlay {
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); /* Semi-transparent black overlay */
  z-index: 2; /* Below the popup but above the rest of the content */
  border-radius:8px;
}

.contactSuccesspopup {
  position: fixed;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  min-width:200px;
  max-height: 500px;
  background-color: #fff; /* White background */
  padding: 30px;
  padding-bottom:60px;

  text-align: start;
  display:flex;
  flex-direction:column;
  justify-content:center;
  border-radius:8px;
  row-gap:10px;
  font-size:20px;
}

.contactSuccesspopup button {
  position: fixed; /* Fixed position to stay in the viewport */
  bottom: 20px; /* Distance from the bottom */
  right: 20px; /* Distance from the right */
  background-color: #FF5A5F;
  z-index: 10;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius:8px;
 
}




@media only screen and (max-width:550px) {

  .btn-outline-primary {
    display: none;
  }

 
  .contactSection
 {
    gap: 15px;
    padding:15px 15px;
   
  }
  
  .contact--form--container {
    width: 100%;
    row-gap: 15px;
  
  }
  .container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .contact--input {
  padding: 2px;
  border: 1.333px solid var(--primary);
  background: var(--white);
  
  
}
  .contact--label {
  
  gap: 5.6px;
  flex: 1 0 0;
  width: 100%;
}
.contactSuccesspopup {
  
  font-size:10px;
}

  }



