
.bookingSection{
  display: flex;
  flex-direction:column;
  padding: 33.333px 85.333px;
  padding-bottom:0px;
  height:100%;
  gap:10px;
  position: relative;
  
}
.bookingContent{
 display: grid;
  grid-template-columns: repeat(3, 1fr); 
 flex-direction:row;
 gap:10px;

}

.leftPart{ 
 grid-column: 1 / span 2;
 box-sizing: border-box;
  
  display:flex;
  flex-direction:column;
  justify-content:start;
  align-items:start;
  
  overflow-y: scroll;
  border-radius: 10px;
 
  font-size:20px;
  gap:10px;
  padding-top:30px;
  padding-bottom:30px;
  
 
}
.custom-date-range-picker{
 padding-top:20px;

}



.rdrDefinedRangesWrapper {
  display: none !important;
}


.rdrMonthsVertical {
  flex-direction: row !important;
  flex-wrap:wrap;
  gap:10px;
  }
 .rdrMonths {
 
  justify-content: center !important;
  
}

.rdrMonthName{
 
 margin-top:-50px;
}
