.rightPart{
  grid-column: 3;
  display:flex;
  flex-direction:column;
  padding: 20px 20px;
  border-radius: 10px;
  align-items:center;
  position: relative;
}
.form{
  margin-top:20px;
  position: sticky;
  top: 150px;
   z-index: 2;
  border:1px solid lightgray;
  padding:20px;
   border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 0 15px rgba(128, 128, 128, 0.3); 
}
.formWidth{
 width:350px;
 display:flex;
 flex-direction:column;
 justify-content:center;
 align-items:center;
 padding-top:20px;
 padding-bottom:20px;
}

.rightPart--title{
  
  margin-bottom:20px;
  font-weight:600;
  text-align:center;
  font-size:25px;
  
}



.rightPart--dateChooseBox{
 display:flex;
 margin-bottom:20px;
 border-radius: 10px;
 background-color:rgb(239, 242, 247);
 font-size:15px;
 height:50px;
 padding:10px;
 align-items:center;
 justify-content:space-between;
  position: relative;
 
 
}


.highlighted-range {
   background-color: #FF5A5F !important;
  
}




.rightPart--checkInBox,
.rightPart--checkOutBox
{
  display: flex;
  flex-direction: column; 
  gap:5px;
  align-items: flex-start; 
 
}
.dateInputbox{
  border:1px solid gray;
  background-color:white;
  padding-left:10px;
  padding-right:10px;
  width:90px;
  border-radius: 5px;
  border-color:lightgray;
}

.rightPart--formSection{
 display:flex;
 flex-direction:column;
 justify-content:center;
 margin-bottom:20px;
 gap:5px;
 color:DarkSlateGrey ;
 border-color:lightgray;


}
.form-style{
  display:flex;
 flex-direction:column;
 justify-content:center;
 margin-bottom:5px;

 gap:5px;
 color:DarkSlateGrey ;


}

.form-style label {
    display: block;
 
}

.form-style input {
    margin-top:5px;
    width: 100%; /* Make the input take up the full width of its container */
    box-sizing: border-box; /* Include padding and border in the input's total width */
}

.inbox {
  border: none;
  background-color:Snow ;
  
 
}



/* Section styling */
.forSummary p{  text-align:Center;
  align-items: center;
  margin-top:5px;
  margin-bottom:5px;
  }

.summary--section{
  display:flex;
  justify-content:space-between;
  text-align:Center;
  align-items: center;
  margin-top:10px;
  font-family: 'Arial', sans-serif;
  color:DarkSlateGrey ;
 
}
.price-section {
  display: flex;
  flex-direction:row;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  
}


.totalPrice {
  margin-top: 20px;
  font-size: 20px;
  font-weight: bold;
  border-top: 2px solid #ddd;
}

.overlay {
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); /* Semi-transparent black overlay */
  z-index: 2; /* Below the popup but above the rest of the content */
  border-radius:8px;
}

.successpopup {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  max-height: 500px;
  background-color: #fff; /* White background */
  padding: 30px;
  padding-bottom:50px;

  text-align: start;
  display:flex;
  flex-direction:column;
  justify-content:center;
  border-radius:8px;
  row-gap:10px;
  font-size:20px;
}
.successpopup ul{
 list-style:none;
  margin: 0;
  padding: 0;
}
.successpopup button {
  position: fixed; /* Fixed position to stay in the viewport */
  bottom: 20px; /* Distance from the bottom */
  right: 20px; /* Distance from the right */
  background-color: #FF5A5F;
  z-index: 10;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius:8px;
 
}


@media only screen and (max-width: 1200px) {
.bookingSection{
  padding: 20.333px 15.333px;
  padding-bottom:0px;
  gap:10px; 
}
 .bookingSection {
    padding-left: 85px ;
    padding-right: 85px ; 
    padding-top:35px;
  }


  .rightPart--formSection,
  .form-style {
    width: 100%; 
  }

  .rightPart--formSection button {
    width: 100%; 
  }

  .forSummary {
    width: 100%; 
  }

