
.info--section{
  display: flex;
  flex-direction:column;
  padding-bottom:0;
 

}
.iconSize{
  max-width: 30px; /* Adjust as needed */
  max-height: 30px; /* Adjust as needed */
  margin-right: 5px; /* Adjust as needed */
  vertical-align: middle;
}

.headIcons{
 display:flex;
  gap: 20px;
  margin-bottom:20px;
 
 

}
.headIconsSpace{
  display: flex;
  align-items: center;
  gap:30px;
 flex-wrap:wrap;
}


.profileSize{
 max-width:60px;
 max-height:60px;
 margin-left: -15px; 

}
.profile{
  display: flex;
  align-items: center;
  border-bottom:1px solid lightgray;
}

.profile span{
 font-weight: bold;
  font-family: sans-serif;
  margin-left: 5px;

}
.headIcons span {
  font-weight: bold;
  font-family: sans-serif;
  margin-left: 5px; /* Adjust as needed for space between image and text */
}


.sectionsPadding{
 border-bottom:1px solid lightgray;
 padding-top:20px;
 padding-bottom:20px;


}



