.bedroomsDispaly{
 display:flex;
 flex-direction:row;
 gap:10px;
 flex-wrap:wrap;
 
}
.singleBedRoom{
 display:flex;
 flex-direction:column;
 gap:10px;
 border:1px solid lightgray;
 padding:20px;
 border-radius: 10px;
}
.singleBedRoom>span>img{
 max-width:30px;
 max-height:30px;
 margin-right:10px;

}

.amenities{
display:flex;
flex-wrap:wrap;
gap:10px;


}
.amenities > * {
 
  flex: 0 0 calc(30% - 10px);  /* 50% width minus the gap */
}





@media only screen and (max-width: 1200px) {
.info--section{
  display: flex;
  padding: 30.333px 85.333px;
  flex-direction:column;
  padding-bottom:0;
 

}
}
@media only screen and (max-width: 1024px) {
.info--section{
  display: flex;
  padding: 30.333px 15.333px 0px 15.333px ;
  
  flex-direction:column;
}
.amenities > * {
 
  flex: 0 0 calc(50% - 0px);  /* 50% width minus the gap */
}

}
@media only screen and (max-width: 767px) {
.info--section{ 
 padding: 10.333px 15.333px 0px 15.333px ;
  padding-bottom: 0px;
}
.bedroomSection{
display:flex;
flex-wrap:wrap;


}
.amenities > * {
  flex: 0 0 calc(50% - 5px);
 /* 50% width minus the gap */
}

}
@media only screen and (max-width: 480px) {
 .info--section{
  padding-top:0px;
  padding-bottom: 0px;
}
}
