.reviewSection{
  display:flex;
  flex-direction:column;
  gap:10px;
  padding: 33.333px 85.333px;
  padding-top:50px;
  height:100%;
  position:relative;
}

.card--content{
   display: flex;
  flex-wrap: wrap;
  gap: 20px;
 
 
  
 
}
.singleCard{
  flex: 0 1 calc(50% - 10px); /* Each card takes up 50% of the container with a gap */
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid lightgray;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
   padding:30px;
  
}
.reviewProfileImg{
 width:40px;
 height:40px;
}

.reviewerTitleBox{
 display:flex;
  align-items: center;
  gap:5px;
}
.card--content>button{

 height:2em;
 font-size:1em;

}
.buttonWidth{
  width:150px;
}

.newCommentContent{
 
   flex: 0 1 calc(50% - 10px); /* Each card takes up 50% of the container with a gap */
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid lightgray;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
 
 
}




 .reviewLabel {
  
  gap: 5.6px;
  flex: 1 0 0;
  width: 100%;
  display:flex;
  flex-direction:column;
  margin-top:10px;
}

.commentInput {
  padding: 1px;
  border: 1.333px solid lightgray;
  background: var(--white);
  
  
}
