.reviewOverlay {
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); /* Semi-transparent black overlay */
  z-index: 2; /* Below the popup but above the rest of the content */
  border-radius:8px;
  
}

.reviewSuccesspopup {
  position: fixed;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  max-height: 500px;
  background-color: #fff; /* White background */
  padding: 30px;
  padding-bottom:60px;

  text-align: start;
  display:flex;
  flex-direction:column;
  justify-content:center;
  border-radius:8px;
  row-gap:10px;
  font-size:20px;
}

.reviewSuccesspopup button {
  position: fixed; /* Fixed position to stay in the viewport */
  bottom: 20px; /* Distance from the bottom */
  right: 20px; /* Distance from the right */
  background-color: #FF5A5F;
  z-index: 10;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius:8px;
 
}


 @media only screen and (max-width: 1024px) {
.reviewSection{

  padding: 33.333px 15.333px;
  height:100%;
 
}
.reviewSuccesspopup {
 
  top: 60%;
  left: 50%;
  font-size:10px;
  min-width:200px;
}

}
  @media only screen and (max-width: 760px) {
  h2{
  font-size:20px;
} 
 .short-button {
 
  max-width:200px; 
  border-color:lightgray;
  white-space: nowrap;
}
.reviewSuccesspopup {
 
  top: 70%;
  left: 50%;
  font-size:10px;
  min-width:200px;
}
.singleCard {
    flex: 0 1 calc(100% - 20px); /* Take up 100% width on smaller screens */
  }

 }

