.locationSection{
  display: flex;
  padding: 20.333px 85.333px;
  flex-direction:column;
  padding-bottom:0;
  gap:10px;
  }  
 
  
  .locationImgSize{
   width:100%;
   height:100%;
   position: relative;
 
  }
  .locationImgSize img {
  width:100%;
  max-height:600px;
  border-radius: 8px;
 
}

.locaticonIcon{
position:absolute;
 max-width:50px;
 max-height:50px;
 top:49%;
 left:44%;
 transition: transform 0.3s, width 0.3s, height 0.3s;
 
}
.locaticonIcon:hover {

  max-width: 40px;
  max-height: 40px; 
}




 @media only screen and (max-width: 1200px) {
.locationSection{
  display: flex;
  padding: 30.333px 15.333px;
  flex-direction:column;
  padding-bottom:0;
  }  
 

.locaticonIcon{
 top:46%;
 left:43%;

 
}
 
 } 
 
 @media only screen and (max-width: 600px) {
 .locationSection{
  
  padding-top: 0.333px;
  
  }  
 
 .locaticonIcon{
 top:37%;
 left:43%;

}
 
 }
 
 
 @media only screen and (max-width: 400px) {
 .locaticonIcon{
 top:26%;
 left:41%;

}
 
 }
